import { ReactComponent as badgeYellow } from '../images/badge-yellow.svg'
import { ReactComponent as badgeViolet } from '../images/badge-violet.svg'
import { ReactComponent as badgeCyan } from '../images/badge-cyan.svg'

const colorsToBadgesMap = {
  'yellow': badgeYellow
  , 'violet': badgeViolet
  , 'cyan': badgeCyan
}

export const RarityCountBar = props => {
  const Badge = colorsToBadgesMap[props.color ?? 'yellow']
  const barClassName = props.color
    ? `rarity-count-bar_color_${props.color}`
    : `rarity-count-bar_color_yellow`

  return (
    <div className={`rarity-count-bar ${barClassName}`} style={{ maxWidth: props.width ?? 300 }}>
      <div className="rarity-count-bar__badge">
        <Badge />
        <span className="rarity-count-bar__level">{props.level ?? 'l1'}</span>
      </div>
      <div className={`rarity-count-bar__count-bar increase-width`}>
        <span className="text text_bold">{props.pcs ?? 2} pcs.</span>
      </div>
    </div>
  )
}
