import incognito from '../images/incognito.png'

export const AnnounceCard = props => {
  const avatar = props?.creator?.avatarUrl ? props.creator.avatarUrl : incognito
  const name = props?.creator?.name ? props.creator.name : 'Coming soon'
  return (
    <div className="announce-card">
      <img src={props.imageUrl} alt="To be announced" className="announce-card__image" />
      <div className="announce-card__info">
        <img src={avatar} alt={name} className="announce-card__avatar" />
        <div>
          <p className="text">Creator</p>
          <p className="text text_bold">Coming soon</p>
        </div>
      </div>
    </div>
  )
}
