import { useLayoutEffect, useRef, useState } from 'react'
import logo from './images/logo.svg'
import nftThumbnail from './images/nft-thumbnail2.png'
import { Artist } from './components/Artist'
import './App.css';
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import { Dropdown } from './components/Dropdown';
import bg from './images/nft-thumbnail2.jpg'
import { AnnounceCard } from './components/AnnounceCard';
import * as constants from './constants'
import { RarityCountBar } from './components/RarityCountBar';

function App() {
  const roadmap = useRef(null)
  const rarity = useRef(null)

  const [scrolledOnRoadmapHeading, setScrolledOnRoadmapHeading] = useState(false)
  const [scrolledOnComingSoon, setScrolledOnComingSoon] = useState(false)
  const [scrolledOnStages, setScrolledOnStages] = useState(false)
  const [scrolledOnRarity, setScrolledOnRarity] = useState(false)

  const [email, setEmail] = useState('')
  const [connectWalletText, setConnectWalletText] = useState('Connect wallet')
  const [buyButtonText, setBuyButtonText] = useState('Buy')
  const [releaseDate, setReleaseDate] = useState(new Date(Date.UTC(2021, 9, 21, 5, 0, 0) - new Date()))

  useLayoutEffect(() => {
    const listener = () => {
      const roadmapOffsetTop = roadmap.current?.offsetTop ?? null
      const roadmapOffsetHeight = roadmap.current?.offsetHeight ?? null
      const rarityOffsetTop = rarity.current?.offsetTop ?? null
      const rarityOffsetHeight = rarity.current?.offsetHeight ?? null


      if (roadmapOffsetTop && roadmapOffsetHeight) {
        if (window.scrollY >= (roadmapOffsetTop - roadmapOffsetHeight) + 50) {
          setScrolledOnRoadmapHeading(true)
        }

        if (window.scrollY >= (roadmapOffsetTop - roadmapOffsetHeight) + 200) {
          setScrolledOnComingSoon(true)
        }

        if (window.scrollY >= (rarityOffsetTop - rarityOffsetHeight) + 200) {
          setScrolledOnStages(true)
        }

        if (window.scrollY >= (rarityOffsetTop - rarityOffsetHeight) + 400) {
          setScrolledOnRarity(true)
        }
      }
    }

    window.addEventListener('scroll', listener)

    const interval = setInterval(() => {
      setReleaseDate(date => new Date(date - 1000))
    }, 1000);

    return () => {
      window.removeEventListener('scroll', listener)
      clearInterval(interval)
    }
  }, [])

  const subscribeOnEmail = async (e) => {
    e.preventDefault()
    const res = await fetch('https://nftonflow.io/subscribe', {
      method: 'POST',
      body: JSON.stringify({
        email
      }),
      headers: {
        'Content-Type': 'application/json'
      }
    })

    if (res.ok) {
      toast('You have been successfully subscribed!')
    } else {
      toast('Something went wrong :(')
    }

    setEmail('')
  }

  return (
    <main>
      <ToastContainer />
      <section className="section main-section">
        <div className="main-section__bg">
          <img src={bg} alt="thumbnail" />
        </div>
        <header className="header main-section__header">
          <a href="/" className="logo header__logo">
            <img src={logo} alt="Logo" />
          </a>
          {/* <span className="text text_bold">
          </span>
          <form onSubmit={subscribeOnEmail}>
            <label className="subscribe header__subscribe">
              <input placeholder="Enter email and subscribe on our news!" type="email" value={email} onChange={(e) => setEmail(e.target.value)} required />
              <button className="button button_primary" type="submit">Send</button>
            </label>
          </form>

          <a href="#" className="text text_bold header__nft-quality-link">NFT Quality</a> */}
          <a
            href="#"
            className="button button_primary"
            id="connectwallet"
            onMouseEnter={() => setConnectWalletText('Coming soon...')}
            onMouseLeave={() => setConnectWalletText('Connect wallet')}
          >
            {connectWalletText}
          </a>
        </header>
        <h2 className="text text_bold heading_level_2" style={{ marginTop: 20, marginBottom: 20 }}>Marketplace for everyone</h2>
        <div className="main-section__promo">
          <div className="card-preview animate__animated animate__fadeInLeft">
            <img src={nftThumbnail} alt="current pre sale" className="card-preview__thumbnail" />
            {/* <div className="card-preview__info">
              <h2 className="heading heading_level_2">Lava flow</h2>
              <div className="d-flex align-center">
                <img src={avatar} alt="avatar" className="card-preview__avatar" />
                <div className="card-preview__data-and-caption">
                  <p className="text">Creator</p>
                  <p className="text text_bold">Coming soon</p>
                </div>
              </div>
            </div> */}
          </div>
          <div className="bidding promo__bidding animate__animated animate__fadeInRight">
            <header className="bidding__header">
              <h3 className="text text_bold heading_level_3">Unique flow avatars collection pre-sale</h3>
              <h1 className="heading heading_level_1">Coming Soon</h1>
            </header>
            <div className="bidding__card">
              <p className="text">Current Price</p>
              <h2 className="heading heading_level_1">0 Flow</h2>
              <div className="sale-stages">
                <div className="sale-stages__pre-sale">
                  <p className="text">Pre Sale</p>
                  <p className="text text_bold">1594 PCS</p>
                </div>
                <div className="sale-stages__public-sale">
                  <p className="text">Public Sale</p>
                  <p className="text text_bold">2000 PCS</p>
                </div>
                <div className="sale-stages__airdrop">
                  <p className="text">Airdrop</p>
                  <p className="text text_bold">500 PCS</p>
                </div>
              </div>
              <p className="text text_bold">Starts in...</p>
              <div className="bidding__timer">
                <div className="bidding__timer-unit">
                  <p className="heading heading_level_2">{releaseDate.getDate()}</p>
                  <p className="text">Days</p>
                </div>
                <div className="bidding__timer-unit">
                  <p className="heading heading_level_2">{releaseDate.getHours()}</p>
                  <p className="text">Hours</p>
                </div>
                <div className="bidding__timer-unit">
                  <p className="heading heading_level_2">{releaseDate.getMinutes()}</p>
                  <p className="text">Mins</p>
                </div>
                <div className="bidding__timer-unit">
                  <p className="heading heading_level_2">{releaseDate.getSeconds()}</p>
                  <p className="text">Secs</p>
                </div>
              </div>
              <a
                href="#"
                className="button button_primary d-block bidding__button"
                onMouseEnter={() => setBuyButtonText('Coming soon...')}
                onMouseLeave={() => setBuyButtonText('Buy')}

              >
                {buyButtonText}
              </a>
              {/* <a href="#" className="button button_flat d-block bidding__button">See Item</a> */}
            </div>
          </div>
        </div>
      </section>

      <section className="section announce">
        <h2 className="heading heading_level_2">To be announced</h2>
        <div className="announce__row">
          <AnnounceCard imageUrl={constants.announce1} />
          <AnnounceCard imageUrl={constants.announce2} />
          <AnnounceCard imageUrl={constants.announce3} />
          <AnnounceCard imageUrl={constants.announce4} />
        </div>
        <div className="announce__row">
          <AnnounceCard imageUrl={constants.announce5} />
          <AnnounceCard imageUrl={constants.announce6} />
          <AnnounceCard imageUrl={constants.announce7} />
          <AnnounceCard imageUrl={constants.announce8} />
        </div>
      </section>

      <section className="section rarity" ref={rarity}>
        <header className={`rarity__header animate__animated ${scrolledOnRarity ? 'animate__fadeInDown' : ''}`}>
          <h2 className="heading heading_level_1">Rarity is inversely proportional to
            <span className="text text_bold text_color_green"> quantity</span>
          </h2>
          <p className="text text_bold">
            If you want to own the rarest specimen, you need to put in a lot of effort.
            <br /> It's worth it.
          </p>
        </header>
        <div className={`rarity__pentagon rarity__pentagon_violet animate__delay-2s animate__animated ${scrolledOnRarity ? 'animate__rotateInDownRight' : ''}`}>
          <constants.pentagonViolet />
        </div>
        <div className={`rarity__pentagon rarity__pentagon_yellow animate__delay-2s animate__animated ${scrolledOnRarity ? 'animate__rotateInDownLeft' : ''} `}>
          <constants.pentagonYellow />
        </div>
        <div className={`rarity__pentagon rarity__pentagon_cyan animate__delay-2s animate__animated ${scrolledOnRarity ? 'animate__rotateInDownRight' : ''}`}>
          <constants.pentagonCyan />
        </div>
        <RarityCountBar width="10%" />
        <RarityCountBar width="20%" level="l2" pcs={4} />
        <RarityCountBar width="30%" level="l3" pcs={8} />
        <RarityCountBar width="40%" level="e1" pcs={16} color="violet" />
        <RarityCountBar width="50%" level="e2" pcs={32} color="violet" />
        <RarityCountBar width="55%" level="e3" pcs={64} color="violet" />
        <RarityCountBar width="60%" level="e4" pcs={128} color="violet" />
        <RarityCountBar width="70%" level="r1" pcs={256} color="cyan" />
        <RarityCountBar width="80%" level="r2" pcs={512} color="cyan" />
        <RarityCountBar width="90%" level="r3" pcs={1024} color="cyan" />
        <RarityCountBar width="100%" level="r1" pcs={2048} color="cyan" />
      </section>

      <section className="section top-artists">
        <header className="top-artists__header">
          <h2 className="heading heading_level_2">Top artists</h2>
          <Dropdown />
        </header>
        <div className="top-artists__row">
          {new Array(4).fill(0).map((_, i) => (
            <Artist index={i + 1} />
          ))}
        </div>
        <div className="top-artists__row">
          {new Array(4).fill(0).map((_, i) => (
            <Artist index={i + 5} />
          ))}
        </div>
        <div className="top-artists__row">
          {new Array(4).fill(0).map((_, i) => (
            <Artist index={i + 9} />
          ))}
        </div>
      </section>

      <section className="section roadmap section_background_gradient" ref={roadmap}>
        <div>
          <h2 className={`heading heading_level_2 roadmap__heading animate__animated ${scrolledOnRoadmapHeading ? 'animate__fadeInDown' : ''}`}>Roadmap</h2>
          <p className="text text_bold roadmap__next-stage"></p>
          <p className={`roadmap__money-need animate__animated ${scrolledOnComingSoon ? 'animate__fadeInDown' : ''}`}>Coming soon</p>
          <div className="roadmap__stages">
            <div className={`stage stage_active roadmap__first-stage animate__animated ${scrolledOnStages ? 'animate__fadeInLeft' : ''}`}>
              <div className="stage__circle">
              </div>
              <h3 className="heading heading_level_3 stage__title">First Stage</h3>
              <p className="stage__description text">The unique Flow avatars collection pre-sale</p>
            </div>
            <div className={`stage animate__animated ${scrolledOnStages ? 'animate__fadeInLeft' : ''}`}>
              <div className="stage__circle">
              </div>
              <h3 className="heading heading_level_3 stage__title">Second Stage</h3>
              <p className="stage__description text">The unique Flow avatars collection public sale</p>
            </div>
            <div className={`stage animate__animated ${scrolledOnStages ? 'animate__fadeInLeft' : ''}`}>
              <div className="stage__circle">
              </div>
              <h3 className="heading heading_level_3 stage__title">Third Stage</h3>
              <p className="stage__description text">Marketplace launch</p>
            </div>
            <div className={`stage animate__animated ${scrolledOnStages ? 'animate__fadeInLeft' : ''}`}>
              <div className="stage__circle">
              </div>
              <h3 className="heading heading_level_3 stage__title">Fours Stage</h3>
              <p className="stage__description text">Marketing and cooperation with the best NFT artists and designers</p>
            </div>
            <div className={`stage animate__animated ${scrolledOnStages ? 'animate__fadeInLeft' : ''}`}>
              <div className="stage__circle">
              </div>
              <h3 className="heading heading_level_3 stage__title">Fifth Stage</h3>
              <p className="stage__description text">To be announced soon</p>
            </div>
          </div>
        </div>
      </section>

      <footer className="footer">
        <section className="section">
          <div className="footer__top">
            <div className="footer__column">
              <p className="text text_bold footer__column-title">Community</p>
              <a href="#" className="text text_bold text_color_grey">Twitter</a>
            </div>
            <div className="footer__column">
              <p className="text text_bold footer__column-title">Platform Navigation</p>
              <a href="#" className="text text_bold text_color_grey">FAQ</a>
            </div>
            <div className="footer__column">
              <p className="text text_bold footer__column-title">Contact Us</p>
              <a href="mailto:contact@nftonflow.io" className="text text_bold text_color_grey">contact@nftonflow.io</a>
            </div>
          </div>
          <div className="footer__bottom">
            <p className="text text_bold footer__text"> NftOnFlow, Inc. All rights reserved.</p>
            <a href="#" className="text text_bold footer__text">Terms</a>
            <a href="#" className="text text_bold footer__text">Privacy
              policy</a>
          </div>
        </section>
      </footer>
    </main>
  );
}

export default App;
